@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@900&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;font-family: 'Montserrat', sans-serif;
    overflow-x: hidden;
}


.side_padding_bg {
    background-color: transparent !important;

}

.navbar {
    background-color: transparent !important;
}

.navbar-brand {
    font-size: 2.2rem !important;
    font-weight: bold;
    color: rgb(23 56 101) !important;
    font-family: 'Noto Sans KR', sans-serif !important;
}
.navbar-brand-big{
    font-size: 3rem !important;
    font-weight: bold;
    color: rgb(23 56 101) !important;
    font-family: 'Noto Sans KR', sans-serif !important;

}

.navbar-nav a{
    font-size: 1.3rem;
    color: rgb(41, 41, 41) !important;
}

.nav-item{
    font-weight: 200;
}
.btn-color{
    background-color: rgb(23 56 101) !important;
    color: #fff !important;
    border: none !important;
    border-radius: 1.3rem !important;
    border-bottom-right-radius: 0 !important;
    padding: 10px 20px !important;
    margin-bottom: 0.5rem;
}

.menu_active,
.navbar-nav a:hover{
    border-bottom: 3px solid rgb(23 56 101);
}

@media(max-width: 991px) {
    .navbar-brand{
        font-size: 1.5rem !important;
    }

    .menu_active{
        border-bottom: none;
    }
}

/*Main Header and body landing section*/
#header{
    width: 100%;
}

#header h1{
    margin: 0 0 10px 0;
    font-size: 48px;
    font-weight: 700;
    overflow-x: unset;
}

.btn-color-orange{
    background-color: rgb(226, 61, 61) !important;
    color: rgb(255, 255, 255) !important;
    border: none !important;
    border-radius: 1.8rem !important;
    border-bottom-right-radius: 0 !important;
    padding: 15px 20px !important;
    margin-bottom: 0.5rem;
}
.btn-color-orange:hover{
    background-color: rgb(5, 60, 148) !important;
    color: rgb(255, 255, 255) !important;
    border: none !important;
    border-radius: 1.8rem !important;
    border-bottom-right-radius: 0 !important;
    padding: 15px 20px !important;
    margin-bottom: 0.5rem;
}

.section-heading{
    text-align: center;
    font-size: 2.2rem !important;
    font-weight: bold;
    margin-bottom: 1rem;
    color: rgb(23 56 101) !important;
}
.section-heading-p{
    text-align: center;
    margin-bottom: 4rem;
}
/* login page*/
.content{
    margin: 8%;
    border-radius: 1.3rem !important;
    border-top: 5px solid rgb(5, 126, 148);
    border-bottom:1px solid rgb(5, 114, 94);
    border-right: 1px solid rgb(5, 114, 94);
    border-left:  1px solid rgb(5, 114, 94);
    background-color: #fff;
    padding: 2rem 1rem 2rem 1rem;
}

.login-text{
    font-style: normal;
    font-weight: 600 !important;
}

.form-control{
    display: block;
    width: 100%;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    border-color: rgb(35, 35, 36) !important;
    border-style: solid !important;
    border-width: 1px 1px 1px 1px !important;
    padding: 5px !important;
    color: #495057;
    height: auto;
    border-radius: 0;
    background-color: #fff;
    background-clip: padding-box;
}

.form-control:focus{
    color: #495057;
    background-color: #fff;
    border-color: #fff;
    outline: 0;
    box-shadow: none;
}

.btn-class{
    width: 100%;
   
    color: #fff !important;
    border: none ;
    border-radius: 0.3rem !important;
    border-bottom-right-radius: 0 !important;
    padding: 10px 20px !important;
    margin-bottom: 0.5rem;
}

.btn-class:hover{
    background-color: transparent !important ;
    color: rgb(7, 6, 6) !important;
    border-width: 1px;
    border-color:rgb(5, 60, 148)  !important ;
    border-radius: 1.3rem !important;
    border-bottom-right-radius: 0 !important;
    padding: 10px 20px !important;
    margin-bottom: 0.5rem;
}

.image-size{
    width: 80%;
    height: 80%;
}

.span-color-green{
    color: rgb(5, 114, 94) !important;
}
.color-green{
    background-color: rgb(5, 114, 94) !important;
}

.span-color-orange{
    color: rgb(226, 61, 61) !important;
}
.color-lightgreen{
    background-color: rgb(57, 161, 161) !important;
}


.social{
    display: flex;
    margin-top: 1rem;
    justify-content: center;
}

.social a{
    display: flex;
    justify-content: center;
    width: 50px;
    height: 50px;
    border: turquoise !important;
    border-radius: 50%;
    margin-right: 0.6rem;
    transition: all .3s ease;

}

.social a img{
    width: 100%;
    height: 100%;
    padding: 0.5rem;
}

/*Cards*/
.projects{
    background-image: linear-gradient(rgb(212, 250, 221),rgb(233, 226, 251)) !important;

}

.btn-card{
    width: 100%;
    background-color: transparent !important ;
    color: rgb(226, 61, 61) !important;
    border-width: 1.8px !important;
    border-color:rgb(255, 0, 34) !important;
    border-radius: 0.1rem !important;
    padding: 10px 20px !important;
    margin-bottom: 0.5rem;
}

.btn-card:checked,
.btn-card:hover{
    width: 100%;
    background-color: rgb(226, 61, 61) !important ;
    color: rgb(255, 255, 255) !important;
    border-width: 1.8px !important;
    border-color:rgb(255, 0, 34) !important;
    border-radius: 0.1rem !important;
    padding: 10px 20px !important;
    margin-bottom: 0.5rem;
}

.section{
    padding-top: 4rem;
    padding-bottom: 3rem;
    background: var(--light);
    
}
.card-wrapper{
    display: grid;
    padding: 2rem;
    grid-template-columns: repeat(3,1fr);
    grid-column-gap: 3rem;
    grid-row-gap: 0rem;
}

/*tutorials*/

.tutorials{
    background-image: linear-gradient(rgb(233, 226, 251),rgb(212, 250, 221));

}

.project-card{
    background: #fff;
    padding:3rem 2rem ;
    position: relative;
    padding-top: 5rem;
    text-align: center;
    transition: all .3s ease;
    border-radius: 1rem;
    border-width: 0.1rem;
}
.project-card:hover{
    background: var(--dark);
}

.project-card img{
    margin-top: -3rem;
    margin-bottom: 2rem;
}







@media (max-width:600px){
    
.section{
        padding-top: 3rem;
        background: var(--light);
    }

.card-wrapper{
        grid-template-columns: repeat(1,1fr);
        padding: 1rem;
        margin-top: 6rem;
    }

}

 

